import { useState } from "react";
import { Link, useNavigate } from 'react-router-dom';

import { Button, Modal } from '@mui/material';

import "./CookieConsent.css";



export default function CookieConsent(){
	console.log("CookieConsent.js: cookieConsent:" + localStorage.getItem("cookieConsent"));
	console.log("CookieConsent.js: user:" + localStorage.getItem("user"));
    
    let userState = localStorage.getItem("user");
    if (userState != null){
      userState = true;
    } else{
    	userState = false;
    }



	let cookieConsentState = localStorage.getItem("cookieConsent");
	if (cookieConsentState == "true"){
      cookieConsentState = true;
	} else{
		cookieConsentState = false;
	}

    const [modalState, setModalState] = useState(true);
    const navigate = useNavigate();

    function HandleClose(){
    	setModalState(false);
    }

    function DeclineCookie(){
        localStorage.setItem("cookieConsent", "false");
    	HandleClose();
    	navigate("/");
    }

    function AcceptCookie(){
        localStorage.setItem("cookieConsent", "true");
    	HandleClose();
    	navigate("/");
    }

    if (userState){
    	return(
    	<Modal open={modalState} onClose={HandleClose}>
		<div id="cookie-consent">
		<p>You are now logged in. 
        If you don't want to use cookies, you can cancel cookies after logging out.</p>
        <Button variant="contained" component={Link} to="/" >Go to main page</Button>
        </div>
		</Modal>
    	);
    } 

	return(
		<Modal open={modalState} onClose={HandleClose}>
		<div id="cookie-consent">
		{cookieConsentState ? <p>You consent our cookies now</p> : <p>You don't consent our cookies now</p>}
		<p>We use cookies to improve your experience on our website. 
		By continuing to use our site, you consent to our use of cookies. 
		You can change your cookie settings at any time on header bar.</p>
		<Button variant="contained" onClick={AcceptCookie}>Accept all</Button>
		<Button variant="text" onClick={DeclineCookie}>Decline all</Button>
        </div>
		</Modal>
		);
}