import { useState } from "react";
import { Link, useNavigate } from 'react-router-dom';
import { Avatar, BottomNavigation, BottomNavigationAction } from '@mui/material';
import LoginRoundedIcon from '@mui/icons-material/LoginRounded';
import LogoutRoundedIcon from '@mui/icons-material/LogoutRounded';
import BorderColorRoundedIcon from '@mui/icons-material/BorderColorRounded';
import CookieRoundedIcon from '@mui/icons-material/CookieRounded';

import $ from "jquery";

import './Body.css';
import "./HeaderBar.css";
import { getCookie } from "./others.js";

export default function HeaderBar(){
    //handle login logout with cookie, local storage
	const navigate = useNavigate();

    //set page cookies
	let localUser = localStorage.getItem("user");
    let cookieUserStatus = false;
	console.log("HeaderBar: localUser:" + localUser);
    let expirationDate = new Date();
	if (localUser != null){
        expirationDate.setTime(expirationDate.getTime() + (30*24*60*60*1000)); // 1 day
        document.cookie = "user=" + localUser + "; expires=" + expirationDate.toUTCString();
        cookieUserStatus = true;
	} else{
        expirationDate.setTime(expirationDate.getTime() + (-10000)); // 1 day
        document.cookie = "user=" + null + "; expires=" + expirationDate.toUTCString();
        cookieUserStatus = false;
    }

    console.log("HeaderBar: " + document.cookie);

    console.log("HeaderBar: " + cookieUserStatus);

    function HandleLogout(event){
        $.post("/api/logout.php", {}, (result) => {
      		if (result.message == "Log out successfully"){
              localStorage.removeItem("user");
              navigate("/");
            }else{
              
            }
      	}, "json");
    }

    let multipleUserLogin = false;
    if (localStorage.getItem("multipleUserLogin") == "true"){
        multipleUserLogin = true;
        localStorage.removeItem("multipleUserLogin");
    } 
    
	return(
		<div id="header-bar">
		{!cookieUserStatus && <BottomNavigation showLabels>
        <BottomNavigationAction label="Login" icon={<LoginRoundedIcon />} component={Link} to="/login" />
        <BottomNavigationAction label="Register" icon={<BorderColorRoundedIcon />} component={Link} to="/register" />
        <BottomNavigationAction label="Set cookies" icon={<CookieRoundedIcon  />} component={Link} to="/cookie-consent" />
        </BottomNavigation>}
        {cookieUserStatus && <BottomNavigation showLabels>
        <Avatar alt={localUser} sx={{position: "relative", top: "8px"}} src="https://magdeleine.co/wp-content/uploads/2022/07/14365468511_7f7f70d739_o-1400x933.jpg" />
        <BottomNavigationAction label="Logout" icon={<LogoutRoundedIcon />} onClick={HandleLogout} />
        <BottomNavigationAction label="Set cookies" icon={<CookieRoundedIcon  />} component={Link} to="/cookie-consent" />
        </BottomNavigation>}
        {multipleUserLogin && <h3>Other user has logged in with your account!</h3>}
		</div>
		);
}

