import { useState } from "react";
import { useNavigate } from 'react-router-dom';
import { Button, FormControl, InputLabel, Input } from '@mui/material';

import $ from "jquery";

import "./Login.css";
import { getCookie } from "./others.js";

export default function Login(){
	let formMax = 100, formMin = 6, formPattern = "^[a-zA-Z0-9]+$";

    const [errorMessage, setErrorMessage] = useState([]);
    const navigate = useNavigate();

    function SubmitLoginForm(event){
      event.preventDefault();

      let user = $("#login-user").val();
      let password = $("#login-password").val();
      let error = [];

      if (user.length > formMax){
        error.push("user name cannot exceed 100 characters!");
      } else if (!user.match(/^[a-zA-Z0-9]+$/)){
        error.push("user name can only contain english character and number!");
      }

      if (password.length < formMin){
        error.push("password cannot be less than 6 characters!");
      } else if (password.length > formMax){
        error.push("password cannot exceed 100 characters!");
      } else if (!password.match(/^[a-zA-Z0-9]+$/)){
        error.push("password can only contain english character and number!");
      } 

      setErrorMessage([...error]);

      if (error.length == 0){
      	let postData = {user: user, password: password};

      	$.post("/api/login.php", postData, (result) => {
      		console.log("Login.js:" + result.message);
      		if (result.result == "success"){
      		  localStorage.setItem("user", result.userCookie);
            localStorage.setItem("cookieConsent", "true");
      		  navigate("/");
      		}else{
      			error.push(result.message);
      			setErrorMessage([...error]);
      		}
      	}, "json");
      }
    }

    function ErrorMessage(props){
      return(
      	<>
      	{props.error && props.error.map((ele) => (<h3>{ele}</h3>))}
      	</>
      	);
    }

	return(
		<div id="login">
		<form id="login-form" onSubmit={SubmitLoginForm}>
		<FormControl variant="standard" className="form">
        <InputLabel htmlFor="login-user">Enter your user name</InputLabel>
        <Input id="login-user" type="text" required />
        </FormControl>
        <FormControl variant="standard" className="form">
        <InputLabel htmlFor="login-password">Enter your password</InputLabel>
        <Input id="login-password" type="password" required />
        </FormControl>
        <ErrorMessage error={errorMessage} />
        <p>By log in, you consent to our use of cookies.</p>
        <Button type="submit" variant="contained" className="button">Login</Button>
        </form>
		</div>
		);
}