import React from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { Outlet, Link } from "react-router-dom";
import Grid from '@mui/material/Unstable_Grid2';
import './index.css';

import { useState, createContext, useEffect } from "react";
import { Button } from '@mui/material';

import HeaderBar from "./HeaderBar.js";
import Header from "./Header.js";
import Body from "./Body.js";
import CookieConsent from "./CookieConsent.js";
import Register from "./Register.js";
import Login from "./Login.js";
import TestMultipleLogin from "./TestMultipleLogin.js";
import TableCase from "./TableCase.js";
import HooksCase from "./HooksCase.js";
import AutocompleteCase from "./AutocompleteCase.js";
import RatingCase from "./RatingCase.js";
import InputValidationCase from "./InputValidationCase.js";
import AvatarCase from "./AvatarCase.js";



const root = ReactDOM.createRoot(document.getElementById('root'));

//<CookieConsent />  

function AppRoute() {
  let cookieConsentState;
  if (localStorage.getItem("cookieConsent") == "true"){
      cookieConsentState = true;
  } else{
      cookieConsentState = false;
  }

  console.log("index.js: cookieConsent:" + localStorage.getItem("cookieConsent"));

  function MainPage(){
    return(
      <>
      <HeaderBar />
      <Header />
      <Body />
      </>
      );
  }

  function ThePage(props){
    return(
      <>
      <HeaderBar />
      <Header />
      <div className="body">
      <Grid container rowSpacing={5} columnSpacing={3}>
      <Grid xs={12} sm={12} lg={12}>
      {props.cardComponent}
      </Grid>
      </Grid>
      </div>
      </>
      );
    }

  return(
    <BrowserRouter>
      <Routes>
      <Route path="/" element={<MainPage />} />
      <Route path="/register" element={<ThePage cardComponent={<Register />} />} />
      <Route path="/login" element={<ThePage cardComponent={<Login />} />} />
      <Route path="/cookie-consent" element={<CookieConsent />} />
      <Route path="/TestMultipleLogin" element={<ThePage cardComponent={<TestMultipleLogin />} />} />
      <Route path="/TableCase" element={<ThePage cardComponent={<TableCase />} />} />
      <Route path="/HooksCase" element={<ThePage cardComponent={<HooksCase />} />} />
      <Route path="/AutocompleteCase" element={<ThePage cardComponent={<AutocompleteCase />} />} />
      <Route path="/RatingCase" element={<ThePage cardComponent={<RatingCase />} />} />
      <Route path="/InputValidationCase" element={<ThePage cardComponent={<InputValidationCase />} />} />
      <Route path="/AvatarCase" element={<ThePage cardComponent={<AvatarCase />} />} />
      </Routes>
    </BrowserRouter>
    );
}



root.render(<AppRoute />);








