import { useState } from "react";
import { useNavigate } from 'react-router-dom';
import { Button } from '@mui/material';

import $ from "jquery";

import "./TestMultipleLogin.css";

export default function TestMultipleLogin(){
	const navigate = useNavigate();

	function HandleMultipleLogin(event){
      $.post("/api/test.php", null, (result) => {
      		console.log("TestMultipleLogin.js:" + result.message);
      		if (result.message == "Log out successfully"){
      		  localStorage.removeItem("user");
      		  localStorage.setItem("multipleUserLogin", "true");
      		  navigate("/");
      		}else{
      		  
      		}
      	}, "json");
    }

	return(
		<div id="test-multiple-login">
		 <p>Login with different browser at the same time and press this Button!</p>
		<Button variant="contained" onClick={HandleMultipleLogin}>If other users log in with same account after you, you will be logged out</Button>
		</div>
		);
}