import { useState } from "react";
import { useNavigate } from 'react-router-dom';
import { Button, FormControl, InputLabel, Input } from '@mui/material';

import $ from "jquery";

import "./Register.css";
import { getCookie } from "./others.js";

export default function Register(){
	let formMax = 100, formMin = 6, formPattern = "^[a-zA-Z0-9]+$";

    const [errorMessage, setErrorMessage] = useState([]);
    const navigate = useNavigate();

    function SubmitForm(event){
      event.preventDefault();

      let user = $("#register-user").val();
      let password = $("#register-password").val();
      let passwordCheck = $("#register-password-check").val();
      let email = $("#register-email").val();
      let error = [];

      if (user.length > formMax){
        error.push("user name cannot exceed 100 characters!");
      } else if (!user.match(/^[a-zA-Z0-9]+$/)){
        error.push("user name can only contain english character and number!");
      }

      if (password.length < formMin){
        error.push("password cannot be less than 6 characters!");
      } else if (password.length > formMax){
        error.push("password cannot exceed 100 characters!");
      } else if (!password.match(/^[a-zA-Z0-9]+$/)){
        error.push("password can only contain english character and number!");
      } else if (password != passwordCheck){
        error.push("password and checked password are not equal!");
      }

      setErrorMessage([...error]);

      if (error.length == 0){
      	let postData = {user: user, password: password, email: email};

      	$.post("/api/register.php", postData, (result) => {
      		console.log("Register.js:" + document.cookie);
          if (result.result == "success"){
            localStorage.setItem("user", result.userCookie);
            localStorage.setItem("cookieConsent", "true");
            navigate("/");
          } else{
            if (result.message == "user name repeat"){
              error.push("The user name is used already!");
              setErrorMessage([...error]);
            }
          }
      	}, "json");
      }
    }

    function ErrorMessage(props){
      return(
      	<>
      	{props.error && props.error.map((ele) => (<h3>{ele}</h3>))}
      	</>
      	);
    }

	return(
		<div id="register">
		<form id="register-form" onSubmit={SubmitForm}>
		<FormControl variant="standard" className="form">
        <InputLabel htmlFor="register-user">Enter your user name</InputLabel>
        <Input id="register-user" type="text" required />
        </FormControl>
        <FormControl variant="standard" className="form">
        <InputLabel htmlFor="register-password">Enter your password</InputLabel>
        <Input id="register-password" type="password" required />
        </FormControl>
        <FormControl variant="standard" className="form">
        <InputLabel htmlFor="register-password-check">Check your password</InputLabel>
        <Input id="register-password-check" type="password" required />
        </FormControl>
        <FormControl variant="standard" className="form">
        <InputLabel htmlFor="register-email">Enter your email</InputLabel>
        <Input id="register-email" type="email" required />
        </FormControl>
        <ErrorMessage error={errorMessage} />
        <p>By registration, you consent to our use of cookies.</p>
        <Button type="submit" variant="contained" className="button">Submit</Button>
        </form>
        </div>
		);
}